@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
/*4, 5, 7*/

body {
  --bg-color: #211f42;
  --seconday-bg-color: #593567;
  --completed-color: #3c2954;
  --bg-color-lighter: #623b71;
  --primary-color: #ffbb57;
  --primary-font-color: #f6f6f6;
  --secondary-font-color: #ffbb57;
  --link-font-color: #ff545c;
  --primary-border-color: #593567;
  --secondary-color: #ff545c;
  --light-grey: #f6f6f6;
  --input-color: #815b90;
  /* TODO fix these colors */
  --delete-color: #a73936;
  --success-color: #58b03e;
  --bold-font: 700;
  --medium-font: 500;
  --regular-font: 400;
  --screen-padding: 80px;
  --primary-font-size: 13px;
  --secondary-font-size: 14px;
  --section-heading-size: 18px;
  --page-heading-size: 26px;
  --border-radius: 3px;
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg-color);
  box-sizing: border-box;
}

.main-section {
  padding: var(--screen-padding);
}


.menu {
  align-items: center;
  background-color: var(--bg-color);
  display: flex;
  justify-content: space-between;
  padding: 20px 80px;
}

@media screen and (max-width: 768px) {
  .menu {
    height: 30px;
  }
}
.mobile-nav {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 30px;
  cursor: pointer;
  height: 30px;
  justify-content: center;
}

.mobile-navbar {
  background-color: var(--light-grey);
  border-radius: 5px;
  display: block;
  height: 2px;
  transition: transform .3s;
  width: 100%;
}

.mobile-navbar__closed:not(:first-child) {
  margin-top: 7px;
}

.mobile-navbar__open {

}

.mobile-navbar__open:first-child {
  margin-bottom: -2px;
  transform: rotate(45deg);
}

.mobile-navbar__open:not(:first-child) {
  transform: rotate(-45deg);
}

@media screen and (min-width: 768px) {
  .mobile-nav {
    display: none;
  }
}
.menu-list-item:not(:first-child) {
  margin-left: 20px;
}
.menu-list {
  color: var(--light-grey);
  display: flex;
  font-size: 16px;
  font-weight: var(--medium-font);
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu-list--logout {
  cursor: pointer;
}

.menu-list a, .menu-list--logout {
  color: inherit;
  text-decoration: none;
}

.menu-list a:not(:first-child), .menu-list--logout {
  margin-left: 20px;
}

.menu-list .menu-list--logout {
  padding-right: 0;
}

.menu-list a, .menu-list--logout {
  color: var(--primary-font-color);
  border-radius: 5px;
  padding: 10px 25px;
}

.menu-list--login {
  color: var(--secondary-font-color);
  border-radius: 5px;
  padding: 10px 25px;
}

.menu-list a.menu-list-item__active {
  color: var(--secondary-font-color);
  transition: color .3s;
}

@media screen and (max-width: 768px) {
  .menu-list-wrapper {
    display: none;
  }
  .menu-list a:last-child {
    padding-right: 25px;
  }
}
.mobile-menu-list-wrapper {
  align-items: center;
  color: var(--primary-color);
  display: flex;
  justify-content: center;
  left: 0;
  position: absolute;
  top: 69px;
  height: calc(100vh - 69px);
  width: 100vw;
}

.mobile-menu-list {
  color: var(--light-grey);
  display: flex;
  font-size: 16px;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

.mobile-menu-list span {
  display: block;
}

.mobile-menu-list a {
  border-radius: 5px;
  color: var(--primary-color);
  padding: 10px 25px;
  margin-top: 20px;
  text-decoration: none;
  display: block;
}

.mobile-menu-list span:first-child a:first-child {
  margin-top: 0;
}

.mobile-menu-list a.mobile-menu-list-item__active {
  color: var(--primary-font-color);
  transition: all .3s;
}

.mobile-menu-list-item__active {

}
.main-section .input-field-wrapper {
  width: 300px;
}

.repeated-tasks {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.repeated-task-wrapper {
  width: 50vw;
}

.repeated-tasks-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 30px;
}

.repeated-tasks-table {
  width: 100%;
}

.repeated-tasks-edit {
  width: 30%;
}

.repeated-tasks-search-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 50px;
}

.repeated-tasks-search-container .button {
  color: var(--bg-color);
}

.repeated-tasks-search-container .input-field-wrapper:first-child {
  margin-bottom: 0;
}

.repeated-task-illustration-container {
  display: flex;
  justify-content: flex-end;
  width: 25vw;
}

.section-title {
  color: var(--primary-font-color);
  font-size: var(--page-heading-size);
}
.input-field-wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
}

.input-field-wrapper:first-child {
  margin-bottom: 20px;
}

.input-field-label {
  margin-bottom: 5px;
  font-weight: var(--medium-font);
  color: var(--primary-font-color);
  font-size: var(--primary-font-size);
}

.input-field {
  background-color: var(--input-color);
  color: var(--primary-font-color);
  border: 1px solid transparent;
  border-radius: var(--border-radius);
  font-weight: var(--regular-font);
  font-family: "Roboto", sans-serif;
  font-size: var(--primary-font-size);
  padding: 10px;
}

.input-field[type="password"] {
  font-family: sans-serif;
}

.input-field-wrapper .icon-path {
  position: absolute;
  right: 10px;
  top: calc((39px - 18px) / 2);
  fill: var(--primary-font-color);
}

.icon-path {
  fill: var(--primary-font-color);
}

.button .icon-path {
  fill: var(--light-grey);
}

.rotate {
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

.repeated-tasks-table-wrapper {
  position: relative;
  background-color: var(--bg-color);
  border-bottom-left-radius: var(--border-radius);
  border-bottom-right-radius: var(--border-radius);
  font-size: var(--primary-font-size);
}

.repeated-tasks-table-loading-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  background-color: var(--bg-color-lighter);
}

.repeated-tasks-table-loading-wrapper .icon-path {
  fill: var(--bg-color)
}

.repeated-tasks-table-empty-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  background: var(--bg-color-lighter);
  color: var(--light-grey);
}

.repeated-tasks-table-header {
  background-color: var(--seconday-bg-color);
  color: var(--link-font-color);
  font-size: var(--primary-font-size);
  display: flex;
  padding: 20px 20px;
  border-top-left-radius: var(--border-radius);
  border-top-right-radius: var(--border-radius);
  font-weight: var(--medium-font);
}

.repeated-tasks-table-header span:nth-child(1) {
  width: 55%;
}

.repeated-tasks-table-header span:nth-child(2) {
  width: 20%;
}

.repeated-tasks-table-header span:nth-child(3) {
  width: 20%;
}

.repeated-tasks-table-header span:nth-child(4) {
  width: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.repeated-tasks-table-row span:nth-child(1) {
  width: 55%;
}

.repeated-tasks-table-row span:nth-child(2) {
  width: 20%;
}

.repeated-tasks-table-row span:nth-child(3) {
  width: 20%;
}

.repeated-tasks-table-row span:nth-child(4) {
  width: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.repeated-tasks-table-row span:nth-child(4) {
  cursor: pointer;
}

.repeated-tasks-table-row {
  align-items: center;
  padding: 20px 20px;
  display: flex;
  background-color: var(--bg-color-lighter);
  position: relative;
  color: var(--light-grey)
}

.repeated-tasks-table-row .icon-path {
  fill: var(--link-font-color);
}

.repeated-tasks-table-row:nth-child(odd) {
  background-color: var(--seconday-bg-color);
}

.repeated-tasks-table-row:last-child {
  border-bottom-left-radius: var(--border-radius);
  border-bottom-right-radius: var(--border-radius);
}
.submenu-wrapper {
  position: absolute;
  right: -220px;
  top: -10px;
  background-color: var(--seconday-bg-color);
  border-radius: var(--border-radius);
  padding: 0 10px;
  z-index: 2;
  text-align: center;
  color: var(--primary-font-color);
  border: 1px solid var(--primary-border-color);
  font-weight: var(--medium-font);
}

.submenu-wrapper:before {
  content: "";
  background-color: var(--seconday-bg-color);
  height: 10px;
  width: 10px;
  display: inline-block;
  position: absolute;
  left: -6px;
  top: calc(50% - 10px);
  transform: rotate(45deg);
  z-index: -1;
  border-left: 1px solid var(--primary-border-color);
  border-bottom: 1px solid var(--primary-border-color);;
}

.submenu-item {
  padding: 12px 20px;
  cursor: pointer;
  z-index: 1;
  color: var(--secondary-font-color)
}

.submenu-item:not(:last-child) {
  border-bottom: 1px solid var(--bg-color-lighter);
}

/*TODO specific delete color?*/
.submenu-item__delete {
  color: var(--link-font-color);
}



.repeated-tasks-edit-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 1100px;
}

.repeated-tasks-edit-heading {
  color: var(--primary-font-color);
  font-size: var(--section-heading-size);
  font-weight: var(--medium-font);
  text-align: center;
  margin: 0;
}

.repeated-tasks-edit-form {
  width: 100%;
  margin-top: 50px;
}

.repeated-task-edit-button-container {
  display: flex;
  padding-top: 50px;
  justify-content: flex-end;
}

.repeated-task-edit-illustration-wrapper {

}

.repeated-task-edit-form-wrapper {
  position: relative;
  width: 500px;
}

.repeated-task-edit-form-wrapper .icon-path {
  fill: var(--link-font-color);
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
}
.textarea-wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
}

.textarea-wrapper:first-child {
  margin-bottom: 20px;
}

.textarea-label {
  margin-bottom: 5px;
  font-weight: var(--medium-font);
  color: var(--primary-font-color);
  font-size: var(--primary-font-size);
}

.textarea {
  background-color: var(--input-color);
  border: 1px solid transparent;
  border-radius: var(--border-radius);
  color: var(--primary-font-color);
  font-weight: var(--regular-font);
  font-family: "Roboto", sans-serif;
  font-size: var(--primary-font-size);
  padding: 10px;
  resize: none;
}
.button {
    background-color: var(--primary-color);
    border: none;
    border-radius: var(--border-radius);
    color: var(--light-grey);
    cursor: pointer;
    font-weight: var(--medium-font);
    font-family: "Roboto", sans-serif;
    font-size: var(--primary-font-size);
    margin: 20px 0 0 0;
    padding: 10px 35px;
}
.repeated-task-schedule-container {
  margin-top: 40px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.repeated-task-schedule-label {
  margin-bottom: 5px;
  font-weight: var(--medium-font);
  color: var(--primary-font-color);
  font-size: var(--primary-font-size);
}

.repeated-task-schedule-custom-container {
  display: flex;
  margin-top: 30px;
}
.repeated-task-schedule-item {
  padding: 10px 20px;
  border-radius: var(--border-radius);
  color: var(--link-font-color);
  font-weight: var(--medium-font);
  font-size: var(--primary-font-size);
  cursor: pointer;
  margin-top: 10px;
  transition: background-color .3s;
  display: inline-table;
}

.repeated-task-schedule-item__active {
  background-color: var(--secondary-color);
  color: var(--light-grey);
}
.repeated-task-schedule-custom-item {
  padding: 10px 20px;
  border-radius: var(--border-radius);
  color: var(--link-font-color);
  font-weight: var(--medium-font);
  font-size: var(--primary-font-size);
  cursor: pointer;
  margin-top: 10px;
  transition: background-color .3s;
}

.repeated-task-schedule-custom-item:not(:first-child) {
  margin-left: 10px;
}

.repeated-task-schedule-custom-item__active {
  background-color: var(--secondary-color);
  color: var(--light-grey);
}
.modal-background {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: var(--seconday-bg-color);
  border-radius: var(--border-radius);
  padding: 40px;
}
.pagination-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: var(--primary-font-size);
  color: var(--primary-font-color);
  margin-top: 25px;
}

.pagination-button-container {
  display: flex;
}

.pagination-button {
  padding: 7px 12px;
  background-color: var(--link-font-color);
  color: var(--primary-font-color);
  border: 1px solid transparent;
  border-radius: var(--border-radius);
  cursor: pointer;
  margin-left: 10px;
  font-weight: var(--medium-font);
}

.pagination-button:first-child {
  margin-left: 0;
}

.pagination-button--active {
  background-color: var(--seconday-bg-color);
}

.pagination-button-text {}


.tasks {
}

.tasks-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tasks-filter-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.tasks-container {
  margin-top: 25px;
  display: flex;
  flex-wrap: wrap;
}

.date-selector {
  width: 250px;
  height: 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--primary-font-color);
  border-radius: var(--border-radius);
  background-color: var(--secondary-color);
  padding: 5px;
  font-size: var(--secondary-font-size);
  font-weight: var(--medium-font);
}

.date-selector .icon-path {
  fill: var(--primary-font-color);
  display: flex;
  align-items: center;
  cursor: pointer;
}

.date-selector__icon {
}

.date-selector__text {
  cursor: pointer;
}

.tasks-filter {
  margin-left: 100px;
}
.task-filter-item {
  margin: 5px 0 5px 5px;
  color: var(--link-font-color);
  font-size: var(--secondary-font-size);
  font-weight: var(--medium-font);
  padding: 0 20px;
  cursor: pointer;
  height: 35px;
  display: flex;
  align-items: center;
}

.task-filter-item:first-child {
  margin-top: 0;
}

.task-filter-item--active {
  color: var(--primary-font-color);
  background-color: var(--secondary-color);
  border-radius: var(--border-radius);
}

.task-item {
  background-color: var(--seconday-bg-color);
  border-radius: var(--border-radius);
  color: var(--primary-font-color);
  padding: 15px;
  margin-left: 15px;
  width: calc(((100% - 5 * 45px) / 6) - 5px);
  position: relative;
}

.task-item--active {
  background-color: #ff545c;
}

.task-item:nth-child(n + 7) {
  margin-top: 15px;
}

.task-item:nth-child(6n + 1) {
  margin-left: 0;
}

.task-item > .icon-path {
  fill: var(--link-font-color);
  cursor: pointer;
  position: absolute;
  top: 15px;
  right: 15px;
}

.task-item--active > .icon-path {
  fill: var(--secondary-bg-color);
  cursor: pointer;
  position: absolute;
  top: 15px;
  right: 15px;
}

.task-item-heading {
  font-weight: var(--medium-font);
  margin-bottom: 20px;
}

.task-item-heading__medium {
  font-size: var(--secondary-font-size);
}

.task-item-heading__small {
  font-size: var(--primary-font-size);
  margin-top: 5px;
}
.task {
  background-color: var(--bg-color);
  padding: 10px;
  border-radius: var(--border-radius);
  display: flex;
  align-items: center;
  position: relative;
  min-height: 40px;
  transition: background-color 0.3s;
}

.task--completed {
  background-color: var(--completed-color);
}

.task--completed > .task-content__text,
.task--completed > .task-content__icon-container {
  opacity: 0.5;
}

.task-content__text {
  font-size: var(--primary-font-size);
}

.task .input-field {
  margin: 0;
  padding: 0;
  width: 95%;
  background-color: var(--bg-color);
}

.task .input-field-wrapper:first-child {
  margin-bottom: 0;
}

.task-content__icon-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  right: 10px;
  top: 10px;
  min-height: 40px;
  opacity: 0.7;
  transition: opacity 0.3s;
  cursor: pointer;
}

.task:hover .task-content__icon-container {
  opacity: 1;
}

.task-content__icon-container > .icon-path {
  fill: var(--link-font-color);
}

.margin-bottom-10 {
