  content: "";
  background-color: var(--seconday-bg-color);
  height: 10px;
  width: 10px;
  display: inline-block;
  position: absolute;
  right: -6px;
  top: 15px;
  transform: rotate(45deg);
  z-index: -1;
  border-right: 1px solid var(--bg-color);
  border-top: 1px solid var(--bg-color);
}

.syncmenu-item {
